<template>
  <div class="market">
    <div class="market-box">
      <div class="sales">
        <div class="sales-title">
          <div>{{language.sell_extraordinary}} </div>
          <p class="world">{{language.largest_financial_NFT}}</p>
        </div>

        <div class="sales-list" @click="recordFn">
          <div>
            <img src="../../images/option/history.png" alt="" />
             {{language.record_list}}
          </div>
        </div>
      </div>

      <div class="sell">
        <m-switch
          class="switch"
          v-model="hedged"
          @exerciseFn="exerciseFn"
          style="margin-top: 20px"
        >
          <div class="exer">
            <div>{{language.only_sold_me}}</div>
          </div>
        </m-switch>

        <div class="search-div">
          <label class="search-box">
            <input
              type="text"
              v-model="searchText"
              :placeholder="language.search_cards"
              @input="searchNftCards"
            />
          </label>
        </div>
      </div>
    </div>
    

    <div class="card" v-if="salesObjectsList.length > 0">
      <div @click="detailsNftFn(item)" v-for="(item, index) in salesObjectsList" :key="index" class="card-box">
        <div class="card-list">
         <img :src="item.defxToken.resUrl" alt="" class="my-img" v-if="item.defxToken && item.defxToken.resUrl " /> 
         <img src="../../images/box.jpg" alt="" class="my-img" v-else /> 
         
          <!-- <div class="card-txt" v-if="item.defxToken">
            <div class="card-img">
              <div class="img-txt">
                <img src="../../images/market/duo.png" alt="" />
                <span v-if="item.defxToken && item.defxToken.grade">{{item.defxToken.grade}}</span>
              </div>
              <div class="img-text">
                <div>
                  <img src="../../images/market/b.png" alt="" /> #{{item.tokenId}}
                </div>
              </div>
            </div>
          </div> -->

        </div>
        <div class="card-list-footer">
          <div>#{{item.tokenId}}</div>
          <p  v-if="item.price">{{ item.price.toFixed(4)}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MSwitch from "@/components/mSwitch";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      searchText: "",
      hedged: false,
      salesObjectsList:[ ],
    };
  },
  components: {
    MSwitch,
  },
  computed: {
    ...mapGetters(["language"]),
  },
  created () {
        this.initQuery("")
  },
  methods: {
      //详情
    detailsNftFn(info){
        this.$router.push({
					path:'/tradeFair/detailsNftCard',
          query: {id:info.id}
			})
    },
    // 查询列表
     initQuery(data){
          this.$api.querymarketLsitFn(data).then((res)=>{
            this.salesObjectsList =  res.salesObjects;
            this.salesObjectsList.forEach( (element )=> {
                 element.price = this.getSalesPrice(element);
             });
          })   
     },
      // 搜索
       searchNftCards(){
        let newArr = [];
         let  inputArr = this.salesObjectsList
         if(this.searchText){
          for (var i = 0; i < inputArr.length; i++) {
           if((inputArr[i].tokenId).includes(this.searchText)){
                newArr.push(inputArr[i])
            }
         }
          this.salesObjectsList = newArr
         }else{
            this.initQuery("")
         }
      }, 
     getSalesPrice(obj) {
           let timeSeconds = Date.parse(new Date()) / 1000;
        if( obj.status == 1) {
            return Number(obj.finalPrice) ;
        } else {
            if((Number(obj.startTime)  + Number(obj.durationTime) ) < Number(timeSeconds)) {
                return  Number(obj.minPrice)
            } else if ( Number(obj.startTime)  >= Number(timeSeconds) ) {
                return  Number( obj.maxPrice);
            } else {
                let  per = ( Number(obj.maxPrice)  - Number(obj.minPrice)) / Number(obj.durationTime);
                return Number(obj.maxPrice) - (Number(timeSeconds) - Number(obj.startTime)) * Number(per);
            }
        }
    },
    exerciseFn(data) {
        if(data){
          return   this.initQuery("")
        }
       this.initQuery("list")
    },
    // 记录
    recordFn() {
      this.$router.push({
        path: "/tradeFair/marketOrderRecordList",
      });
    },
  },
};
</script>
